import React from 'react';
import Helmet from 'react-helmet';

import Layout from '../components/layout';
import PageNavigation from '../components/page-navigation';

import favicon from '../assets/images/website-icon.png';

const Index = () => (
  <Layout>
    <section id="header">
      <div className="inner">
        <h1>謎解き、やっていきませんか？</h1>
      </div>
    </section>
    <Helmet>
      <title>はじまり | 氷蔵姉妹の挑戦</title>
      <meta name="twitter:card" content="summary" />
      <meta name="twitter:site" content="@reizouko_online" />
      <meta property="og:url" content="https://contents.reizouko.online/riddle20200831/" />
      <meta property="og:title" content="氷蔵姉妹の挑戦 復刻版" />
      <meta property="og:description" content="2020/8/31までの期間限定で実施していた謎解きの復刻版です" />
      <meta property="og:image" content={`https://contents.reizouko.online/riddle20200831${favicon}`} />
    </Helmet>

    <section id="one" className="main style1">
      <div className="grid-wrapper">
        <div className="col-12">
          <p>
            この謎解きは、2020/8/31までの期間限定で公開していた謎解き「氷蔵姉妹の挑戦」を復刻したものです。<br/>
            当時と一部コンテンツが異りますが、謎の中身は基本的に同じです。<br/>
            また当時は、Twitterアカウント <a href="https://twitter.com/ryoka_hikura" target="_blank" rel="noopener noreferrer">@ryoka_hikura</a> のための企画であり、<br/>
            ストーリーがあり、クリア後のページで新規イラスト限定お披露目というのがありましたが、<br/>
            イラストはもう公開済みなので、これらを廃止し、別のクリアページを設けました。
          </p>
          <p>
            ま、細かいことはいいですね。それでは次のページから謎解きスタートです！
          </p>
          <PageNavigation next="/question/" />
        </div>
      </div>
      <div className="grid-wrapper">
        <div className="col-12">
        </div>
      </div>
    </section>
  </Layout>
);

export default Index;
